<template>
  <div class="d-flex flex-column overflow-auto">
    <!-- <b-table striped hover :items="items"></b-table> -->
    <select v-model="dataSwitch" class="form-control">
      <option :value="items2">30801</option>
      <option :value="items3">30802</option>
      <option :value="items4">30803</option>
    </select>
    <b-table striped hover :fields="fields" :items="dataSwitch"></b-table>
    <div class="d-flex flex-fill justify-content-center">
      <div>
        <b-form-datepicker
          v-model="date1"
          :min="minDate"
          :max="maxDate"
          class="mb-2"
        ></b-form-datepicker>
      </div>
      <div>
        <b-form-datepicker
          v-model="date2"
          :min="minDate"
          :max="maxDate"
          class="mb-2"
        ></b-form-datepicker>
      </div>
    </div>
    <div class="d-flex flex-fill justify-content-center align-items-center">
      <select v-model="dataSwitch2" class="form-control">
        <option :value="0">全部</option>
        <option :value="30801">30801</option>
        <option :value="30802">30802</option>
        <option :value="30803">30803</option>
      </select>
    </div>
    <div class="d-flex flex-fill justify-content-center">
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
      ></b-pagination>
    </div>
    <b-table
      :items="totalData_table2"
      :fields="fields_total"
      :per-page="perPage"
      :current-page="currentPage"
    ></b-table>
  </div>
</template>

<script>
// import errorData from '../assets/30802dataerrortable.json';
import moment from 'moment';
import errorData2 from '../assets/midataerrortable.json';
import totalData from '../assets/MITotalData.json';

export default {
  data() {
    return {
      dataSwitch: [],
      // items: errorData,
      allError: [],
      perPage: 30,
      currentPage: 1,
      totalData_table: [],
      dataSwitch2: 0,
      fields: [
        {
          key: 'classroom',
          label: '教室名稱',
          sortable: true,
        },
        {
          key: 'host',
          label: '電腦名稱',
          sortable: false,
        },
        {
          key: 'count',
          label: '錯誤次數',
          sortable: true,
        },
      ],
      fields_total: [
        {
          key: 'date',
          label: '日期',
          sortable: true,
        },
        {
          key: 'classroom',
          label: '教室名稱',
          sortable: false,
        },
        {
          key: 'host',
          label: '電腦名稱',
          sortable: true,
        },
        {
          key: 'use_total_time',
          label: '使用時間',
          sortable: true,
        },
        {
          key: 'count',
          label: '使用次數',
          sortable: true,
        },
        {
          key: 'per_average_time',
          label: '平均使用時間',
          sortable: true,
        },
      ],
      items2: [],
      items3: [],
      items4: [],
      date1: '',
      date2: '',
    };
  },
  created() {
    this.items2 = errorData2.filter((e) => e.classroom === 30801);
    this.items3 = errorData2.filter((e) => e.classroom === 30802);
    this.items4 = errorData2.filter((e) => e.classroom === 30803);
    // errorData2.forEach((e) => {
    //   this.allError.push({
    //     classroom: e.classroom,
    //     host: e.host,
    //     count: e.count,
    //   });
    // });
    totalData.forEach((e) => {
      this.totalData_table.push({
        date: moment(new Date(e.date)).format('YYYY-MM-DD'),
        classroom: e.classroom,
        host: e.host,
        use_total_time: e.use_total_time.toFixed(2),
        count: e.count,
        per_average_time: e.per_average_time.toFixed(2),
      });
    });
  },
  mounted() {
    this.dataSwitch = this.items2;
    this.date1 = this.minDate;
    this.date2 = this.maxDate;
  },
  computed: {
    rows() {
      return this.totalData_table2.length;
    },
    totalData_table2() {
      return this.totalData_table.filter((e) => {
        if (this.dataSwitch2 !== 0) {
          if (e.classroom === this.dataSwitch2) {
            return moment(e.date).isBetween(this.date1, this.date2, null, []);
          }
        } else {
          return moment(e.date).isBetween(this.date1, this.date2, null, []);
        }
      });
    },
    minDate() {
      return moment.min(this.totalData_table.map((e) => moment(e.date))).format('YYYY-MM-DD');
    },
    maxDate() {
      return moment.max(this.totalData_table.map((e) => moment(e.date))).format('YYYY-MM-DD');
    },
  },
};
</script>

