<template>
  <div id="app">
    <div class="container">
      <div class="Chart__list row no-gutters">
        <div
          class="Chart col-12 d-flex flex-column
         flex-fill"
        >
          <!-- <line-example></line-example> -->
          <div class="d-flex flex-column">
            <div class="d-flex justify-content-start">
              <div class="form-group flex-fill justify-content-start">
                <label for="selectClass">選擇教室</label>
                <select v-model="switchClass_line" class="form-control" name="selectClass">
                  <option :value="[false, false, false]">全部</option>
                  <option :value="[false, true, true]">30801</option>
                  <option :value="[true, false, true]">30802</option>
                  <option :value="[true, true, false]">30803</option>
                </select>
              </div>
            </div>
            <line2 :switchClass="switchClass_line"></line2>
          </div>
          <div class="d-flex flex-column">
            <div class="form-group flex-fill justify-content-start">
                <label for="selectClass">選擇教室</label>
                <select v-model="filterData_bar" class="form-control" name="selectClass">
                  <option selected :value="{title:'30801',data:average}">30801</option>
                  <option :value="{title:'30802',data:average2}">30802</option>
                  <option :value="{title:'30803',data:average3}">30803</option>
                </select>
              </div>
            <bar2 :PropsAverage="filterData_bar" :date_bar="date_bar"></bar2>
          </div>
          <div><bar3></bar3></div>
          <div><tree></tree></div>
          <div><error></error></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import LineExample from '../components/HelloWorld.vue';
// eslint-disable-next-line no-unused-vars
import _ from 'lodash';
import bar2 from '../components/bar2.vue';
import bar3 from '../components/bar3.vue';
import tree from '../components/treemap30802.vue';
import error from '../components/errortable30802.vue';
import line2 from '../components/line2.vue';
// import test from '../assets/test.json';
// eslint-disable-next-line no-unused-vars
import middata from '../assets/midatalinechart.json';
import barData from '../assets/midatabarchart.json';

export default {
  name: 'app',
  data() {
    return {
      data: middata,
      datacollection: null,
      data_bar: barData,
      switchClass_line: [false, false, false],
      filterData_bar: {},
      date: [],
      date_bar: [],
      total: [],
      total2: [],
      total3: [],
      average: [],
      average2: [],
      average3: [],
    };
  },
  components: {
    // LineExample,
    bar2,
    bar3,
    tree,
    error,
    line2,
  },
  created() {
    this.data.forEach((element) => {
      if (element.classroom === 30801) {
        this.date.push(new Date(element.date));
        this.total.push(element.use_total_time);
      } else if (element.classroom === 30802) {
        this.total2.push(element.use_total_time);
      } else if (element.classroom === 30803) {
        this.total3.push(element.use_total_time);
      }
    });
    const aaa = this.data_bar.filter((e) => e.classroom === 30801);
    const bbb = this.data_bar.filter((e) => e.classroom === 30802);
    const ccc = this.data_bar.filter((e) => e.classroom === 30803);
    this.date_bar = aaa.map((e) => e.weekday);
    this.average = aaa.map((e) => e.use_average_time.toFixed(2));
    this.average2 = bbb.map((e) => e.use_average_time.toFixed(2));
    this.average3 = ccc.map((e) => e.use_average_time.toFixed(2));
    this.filterData_bar = { title: '30801', data: this.average };
  },
};
</script>
