<script>
// import { Line } from 'vue-chartjs';
import { Bar } from 'vue-chartjs';
// eslint-disable-next-line no-unused-vars
import _ from 'lodash';
// import test from '../assets/test.json';
import barData from '../assets/midatabarchart.json';

export default {
  // extends: Line,
  extends: Bar,
  data() {
    return {
      data: barData,
      date: [],
      average: [],
      average2: [],
      average3: [],
    };
  },
  mounted() {
    // this.data.forEach((element) => {
    //   // console.log(element.weekday);
    //   this.date.push(element.weekday);
    //   this.average.push(element.use_average_time);
    // });
    const aaa = this.data.filter((e) => e.classroom === 30801);
    const bbb = this.data.filter((e) => e.classroom === 30802);
    const ccc = this.data.filter((e) => e.classroom === 30803);
    this.date = aaa.map((e) => e.weekday);
    this.average = aaa.map((e) => e.use_average_time);
    this.average2 = bbb.map((e) => e.use_average_time);
    this.average3 = ccc.map((e) => e.use_average_time);
    console.log(aaa);
    this.renderChart(
      {
        labels: this.date,
        datasets: [
          {
            label: '30801',
            data: this.average,
            backgroundColor: 'red',
          },
          {
            label: '30802',
            data: this.average2,
            backgroundColor: 'blue',
          },
          {
            label: '30803',
            data: this.average3,
            backgroundColor: 'green',
          },
        ],
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: '所有教室平均使用時間堆疊長條圖',
        },
        tooltips: {
          bodyFontSize: 16,
          titleFontSize: 18,
          callbacks: {
            label(tooltipItem, data) {
              let label = data.datasets[tooltipItem.datasetIndex].label || '';
              if (label) {
                label += ': ';
              }
              label = Math.round(tooltipItem.yLabel * 100) / 100;
              console.log(label);
              return `平均${label}小時`;
            },
          },
        },
        scales: {
          xAxes: [{
            scaleLabel: {
              display: true,
              labelString: '星期',
            },
          },
          ],
          yAxes: [
            {
              scaleLabel: {
                display: true,
                labelString: '平均時數',
              },
            },
          ],
        },
      },
    );
  },
};
</script>
